.footer {
    display: flex; /* Use flexbox for horizontal alignment */
    justify-content: space-around; /* Space out the columns evenly */
    background-color: #f8f8f8; /* Light background color */
    color: #333; /* Dark text color */
    font-family: Arial, sans-serif; /* Font style */
    padding: 40px 100px 70px;
}

.footerColumn {
    align-content: flex-start;
    text-align: start;
    width: 25%;
    margin: 0 20px; /* Margin between columns */
}

.footerColumn h3 {
    margin-bottom: 10px; /* Space between title and text */
    color: #ff621f; /* Title color */
}

.footerColumn p,
.footerColumn ul {
    margin: 5px 0; /* Space between paragraphs and list items */
}

.socialMedia {
    list-style: none; /* Remove bullet points */
    padding: 0; /* Remove padding */
}

.socialMedia li {
    margin: 5px 0; /* Space between social media links */
    display: flex; /* Use flexbox */
    align-items: center;
}

.socialMedia li > img {
    width: 25px;
    height: 25px;
    border-radius: 20px;
    margin-right: 10px;
}

.socialIcon {
    color: black; /* Orange color for social media links */
    text-decoration: none; /* Remove underline from links */
}

.socialIcon:hover {
    text-decoration: underline; /* Underline on hover */
}

.legalLinks {
    list-style: none; /* Remove bullet points */
    padding: 0; /* Remove padding */
}

.legalLinks li {
    margin: 5px 0; /* Space between legal links */
}

.legalLinks li a {
    color: black;
    text-decoration: none;
}

@media (max-width: 768px) {
    .footer {
        flex-direction: column;
        padding: 20px;
    }

    .footerColumn {
        width: 100%;
        margin: 10px 0 0;
        text-align: center;
    }

    .footerColumn > ul > li > a {
        font-size: 14px;
    }

    .footerColumn > p {
        font-size: 14px;
    }

    .footerColumn > h3 {
        margin-bottom: 0;
    }

    .socialMedia {
        display: flex;
        flex-direction: row;
        justify-content: center;
        gap: 10px;
    }
}
